.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
		.orn-1 {
			position: absolute;
			top: 5%;
			left: 0;
			width: 280px;
			transform: translate(-50%, 0);
			@include max-md {
				width: 240px;
			}
			@include max-xs {
				width: 220px;
			}
			.orn-2 {
				position: relative;
				width: 180px;
				transform: translate(50%, 10%);
				@include max-lg {
					width: 160px;
				}
				@include max-md {
					width: 140px;
					transform: translate(60%, 10%);
				}
				@include max-xs {
					width: 120px;
				}
				img {
					transform-origin: 0% 100%;
				}
				.orn-3 {
					position: absolute;
					top: 0;
					left: 0;
					width: 180px;
					transform: translate(-20%, -40%) rotate(20deg);
					@include max-lg {
						width: 160px;
					}
					@include max-md {
						width: 140px;
					}
					@include max-xs {
						width: 120px;
					}
					img {
						transform-origin: 50% 80%;
					}
				}
				.orn-4 {
					position: relative;
					width: 220px;
					transform: translate(-40%, 0) rotate(20deg);
					@include max-lg {
						width: 200px;
					}
					@include max-md {
						width: 180px;
					}
					@include max-xs {
						width: 160px;
					}
					img {
						transform-origin: 50% 100%;
					}
				}
			}
		}
		.orn-5 {
			position: absolute;
			top: 25%;
			right: 0;
			width: 180px;
			transform: translateX(20%) scaleX(-1);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 0% 100%;
			}
			.orn-6 {
				position: absolute;
				top: 0;
				transform: translate(-30%, -30%) rotate(20deg);
				img {
					transform-origin: 50% 100%;
				}
			}
			.orn-7 {
				position: relative;
				width: 380px;
				transform: translate(-65%, -50%) scaleX(-1);
				@include max-lg {
					width: 360px;
				}
				@include max-md {
					width: 320px;
				}
				@include max-xs {
					width: 280px;
				}
				img {
					transform-origin: 50% 50%;
				}
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translateX(-30%);
		@include max-md {
			width: 260px;
		}
		@include max-xs {
			width: 240px;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translateX(30%);
		@include max-md {
			width: 260px;
		}
		@include max-xs {
			width: 240px;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		background: var(--color-gradient);
		border-radius: var(--border-radius);
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		padding: 2rem;
		overflow: hidden;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			@include max-md {
				width: 100%;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 10px;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-image: url(../images/border-substract.png);
					background-size: 100% 100%;
					background-repeat: no-repeat;
					content: "";
				}
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					mask-image: url(../images/mask-substract.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 3rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.15rem 0.25rem;
			border-radius: 5px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
