.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-50%, 40%) scaleY(-1) rotate(20deg);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
	}
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
