.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.elements-wrapper-absolute {
		top: 0;
		height: calc(100% - 3rem);
		.background-element {
			img {
				object-position: 50% 70%;
				transform: scaleX(-1);
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
		transform: translate(-40%, -10%) scaleX(-1);
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto -3rem;
		.reminder-body {
			position: relative;
			background: $stroke-gradient;
			padding: 3rem;
			mask-image: url(../images/mask-substract.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			z-index: 2;
			&::before {
				position: absolute;
				top: 2px;
				left: 2px;
				bottom: 2px;
				right: 2px;
				background-image: url(../images/bg-main.webp);
				background-size: 100%;
				background-position: 50% 100%;
				background-repeat: no-repeat;
				mask-image: url(../images/mask-substract.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				content: "";
			}
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
