.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	overflow: hidden;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 101%;
	}
	.orn-2 {
		position: absolute;
		top: 55%;
		left: 0;
		width: 48%;
		transform: translate(-30%, -100%);
		@include max-lg {
			top: 36%;
			width: 60%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 60%;
		left: 50%;
		width: 40%;
		transform: translate(-40%, -100%);
		@include max-lg {
			top: 40%;
			width: 50%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 55%;
		right: 0;
		width: 40%;
		transform: translate(30%, -100%) scaleX(-1);
		@include max-lg {
			top: 36%;
			width: 60%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 30%;
		transform: translate(-30%, -30%);
		@include max-lg {
			width: 50%;
			transform: translate(-15%, -10%);
		}
	}
	.orn-6 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 50%;
		transform: translate(-50%, -50%);
		@include max-lg {
			top: 40%;
			width: 70%;
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 35%;
		transform: translate(30%, -20%);
		@include max-lg {
			width: 50%;
			transform: translate(30%, 20%);
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 40%;
		transform: translateX(-30%);
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 40%;
		transform: translateX(30%);
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(0, 50%);
		@include max-lg {
			width: 180px;
			transform: translate(-20%, 50%);
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 240px;
			transform: translate(60%, 20%);
			@include max-lg {
				width: 200px;
			}
			@include max-md {
				width: 180px;
				transform: translate(50%, 30%);
			}
			@include max-xs {
				width: 140px;
			}
			img {
				transform-origin: 50% 50%;
			}
			.butterfly-1 {
				position: absolute;
				top: 0;
				left: 50%;
				width: 100px;
				transform: translate(-50%, -100%) rotate(20deg);
				@include max-lg {
					width: 80px;
					transform: translate(-50%, -80%) rotate(20deg);
				}
				@include max-md {
					width: 60px;
				}
			}
			.butterfly-2 {
				position: absolute;
				top: 23%;
				right: 0;
				width: 80px;
				transform: translate(90%, 0) rotate(-25deg);
				@include max-lg {
					transform: translate(30%, 20%) rotate(-25deg);
					width: 60px;
				}
				@include max-md {
					width: 40px;
				}
			}
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 5%;
		width: 240px;
		transform: translate(0, 60%) scaleX(-1);
		@include max-lg {
			right: 0;
			width: 220px;
			transform: translate(20%, 60%) scaleX(-1);
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 140px;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(100%, -50%);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
				transform: translate(80%, -50%);
			}
			@include max-xs {
				width: 100px;
			}
			img {
				transform-origin: 0% 100%;
			}
			.butterfly-1 {
				position: absolute;
				top: 0;
				left: 0;
				width: 80px;
				transform: translate(0, -10%) scaleX(-1);
				@include max-lg {
					width: 60px;
				}
				@include max-md {
					width: 40px;
				}
			}
		}
	}
	.orn-14 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(100%, 20%);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
			transform: translate(120%, 20%);
		}
		@include max-xs {
			width: 120px;
		}
	}
	.orn-15 {
		position: absolute;
		bottom: 23%;
		left: 62%;
		width: 80px;
		@include max-lg {
			bottom: 52%;
			left: 65%;
			width: 70px;
		}
		@include max-md {
			width: 50px;
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		.header {
			position: relative;
			display: grid;
			grid-template-rows: auto 1fr;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				color: var(--color-secondary);
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.foreground-element {
					width: 100%;
					height: 100%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					&::before {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						background-color: rgba($color: #fff, $alpha: 0.5);
						filter: blur(5px);
						content: "";
					}
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
