.cover-section {
	position: fixed;
	z-index: 1000;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transition: transform 1s ease-out;
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translateY(30%);
		@include max-lg {
			transform: translate(0);
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 50%;
		transform: translate(-50%, 0);
		@include max-lg {
			width: 80%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 50%;
		transform: translate(-30%, -93%);
		@include max-lg {
			width: 80%;
			transform: translate(-40%, -93%);
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 40%;
		transform: translate(-50%, -93%);
		@include max-lg {
			width: 60%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		right: 0;
		width: 45%;
		transform: translate(30%, -93%) scaleX(-1);
		@include max-lg {
			width: 75%;
			transform: translate(40%, -93%) scaleX(-1);
		}
	}
	.orn-6 {
		position: absolute;
		top: 30%;
		left: 50%;
		width: 50%;
		transform: translate(-50%, -50%);
		@include max-lg {
			top: 40%;
			width: 80%;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				text-align: center;
				background: $stroke-gradient;
				mask: var(--mask);
				padding: 2px;
				.cover-description {
					padding: 3rem 2rem;
					text-align: center;
					background: var(--color-primary);
					mask: var(--mask);
					@include max-md {
						padding: 3rem 1rem;
					}
					* {
						position: relative;
					}
					.title {
						color: var(--color-dark);
					}
					.greeting-wrapper {
						position: relative;
						@include max-md {
							font-size: 14px;
						}
						h6 {
							font-weight: 700;
						}
					}
					h6 {
						font-weight: 600;
					}
				}
			}
			.widget-element {
				width: 100%;
			}
		}
	}
}
