.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 101%;
	}
	.orn-2 {
		position: absolute;
		top: 55%;
		left: 0;
		width: 48%;
		transform: translate(-30%, -100%);
		@include max-lg {
			top: 36%;
			width: 60%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 60%;
		left: 50%;
		width: 40%;
		transform: translate(-40%, -100%);
		@include max-lg {
			top: 40%;
			width: 50%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 55%;
		right: 0;
		width: 40%;
		transform: translate(30%, -100%) scaleX(-1);
		@include max-lg {
			top: 36%;
			width: 60%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 30%;
		@include max-lg {
			width: 50%;
			transform: translate(-15%, 0%);
		}
	}
	.orn-6 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 50%;
		transform: translate(-50%, -50%);
		@include max-lg {
			top: 40%;
			width: 70%;
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 35%;
		transform: translate(30%, 0);
		@include max-lg {
			width: 50%;
			transform: translate(30%, 0);
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 30%;
		transform: translateX(-30%);
		@include max-lg {
			width: 40%;
			transform: translateX(-20%);
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 30%;
		transform: translateX(30%);
		@include max-lg {
			width: 40%;
			transform: translateX(20%);
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(0, 50%);
		@include max-lg {
			width: 180px;
			transform: translate(-20%, 50%);
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 220px;
			transform: translate(60%, 25%);
			@include max-lg {
				width: 200px;
			}
			@include max-md {
				width: 180px;
				transform: translate(50%, 30%);
			}
			@include max-xs {
				width: 160px;
			}
			img {
				transform-origin: 50% 50%;
			}
			.butterfly-1 {
				position: absolute;
				top: 0;
				left: 50%;
				width: 100px;
				transform: translate(-50%, -80%) rotate(20deg);
				@include max-md {
					width: 80px;
				}
			}
			.butterfly-2 {
				position: absolute;
				top: 23%;
				right: 0;
				width: 80px;
				transform: translate(80%, 0) rotate(-25deg);
				@include max-md {
					width: 60px;
				}
				@include max-xs {
					width: 40px;
				}
			}
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 5%;
		width: 240px;
		transform: translate(0, 60%) scaleX(-1);
		@include max-lg {
			right: 0;
			width: 220px;
			transform: translate(20%, 60%) scaleX(-1);
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(100%, -50%);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 100px;
			}
			img {
				transform-origin: 0% 100%;
			}
			.butterfly-1 {
				position: absolute;
				top: 0;
				left: 0;
				width: 80px;
				transform: translate(0, -10%) scaleX(-1);
				@include max-lg {
					width: 60px;
				}
				@include max-md {
					width: 40px;
				}
			}
		}
	}
	.orn-14 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(100%, 20%);
	}
	.orn-15 {
		position: absolute;
		bottom: 23%;
		left: 62%;
		width: 80px;
		@include max-md {
			width: 40px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 2px;
			background: $stroke-gradient;
			mask: var(--mask);
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask: var(--mask);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			background: $stroke-gradient;
			mask: var(--mask);
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
			&::before {
				position: absolute;
				top: 2px;
				right: 2px;
				bottom: 2px;
				left: 2px;
				background-image: url(../images/bg-venue-card.webp);
				background-size: 100%;
				background-position: 50% 80%;
				background-repeat: no-repeat;
				background-origin: content-box;
				mask: var(--mask);
				content: "";
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
